/**
 * Severity configuration for error banners.
 * Maps each BannerSeverity level to its icon, ARIA attributes,
 * auto-dismiss duration, and colour tokens.
 */

import { Info, AlertTriangle, AlertCircle, XCircle } from 'lucide-react';
import type { LucideIcon } from 'lucide-react';
import type { BannerSeverity } from '@/types/error-banner';

export interface SeverityConfigEntry {
  icon: LucideIcon;
  role: 'status' | 'alert';
  ariaLive: 'polite' | 'assertive';
  /** Auto-dismiss duration in milliseconds. 0 means no auto-dismiss. */
  duration: number;
  borderColor: string;
  iconColor: string;
}

export const SEVERITY_CONFIG: Record<BannerSeverity, SeverityConfigEntry> = {
  info: {
    icon: Info,
    role: 'status',
    ariaLive: 'polite',
    duration: 5000,
    borderColor: 'hsl(var(--brand-tufts))',
    iconColor: 'hsl(var(--brand-tufts))',
  },
  warning: {
    icon: AlertTriangle,
    role: 'status',
    ariaLive: 'polite',
    duration: 8000,
    borderColor: 'hsl(var(--brand-warning))',
    iconColor: 'hsl(var(--brand-warning))',
  },
  error: {
    icon: AlertCircle,
    role: 'alert',
    ariaLive: 'assertive',
    duration: 10000,
    borderColor: 'hsl(var(--destructive))',
    iconColor: 'hsl(var(--destructive))',
  },
  critical: {
    icon: XCircle,
    role: 'alert',
    ariaLive: 'assertive',
    duration: 0,
    borderColor: 'hsl(var(--brand-critical))',
    iconColor: 'hsl(var(--brand-critical))',
  },
};
