/**
 * D0 Foundational Utilities
 *
 * This module provides infrastructure-level helpers that sit below
 * the component layer. It is part of D0 (Design Foundation) and
 * must not depend on any D1+ artifacts.
 *
 * cn  Tailwind class-name composition utility.
 *      Merges class values via clsx and resolves Tailwind conflicts
 *      via tailwind-merge. Used by all shadcn/ui primitives.
 *
 * formatCurrency  Re-exported from @/utils/currencyFormat for
 *                  path-alias convenience. Canonical source lives
 *                  in src/utils/currencyFormat.ts.
 */

import { type ClassValue, clsx } from "clsx";
import { twMerge } from "tailwind-merge";

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

export { formatCurrency } from "@/utils/currencyFormat";
